import React, {Component} from 'react'

class TopNav extends Component {
  render() {
    return (
      <div className="top-nav">
        <div className="icon-back">
          <a href="javascript:"></a>
        </div>
        <div className="nav-title"></div>
      </div>
    )
  }
}

export default TopNav


